.StepCounter {
    --active-color: #5125db;
    --inactive-color: #c6c5c5;
    --transition-track-time: 200ms;
    --transition-track-delay: 100ms;
    --transition-bullet-time: 100ms;
    --transition-bullet-delay: 220ms;
    --transition-label-time: 150ms;
    --transition-label-delay: 150ms;
    background: white;
    list-style-type: none;
    padding: 0;
    margin: 0;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-line-pack: center;
        align-content: center;
    position: relative;
    height: 40px;
    padding-top: 40px;
}

.StepCounterItem.is-active .StepCounterItem__bullet {
    border-color: var(--active-color);
}

.StepCounterItem {
    -ms-flex-positive: 1;
        flex-grow: 1;
    position: relative;
}

.StepCounterItem:first-child:before,
.StepCounterItem:first-child:after {
    display: none;
}

.StepCounterItem:before,
.StepCounterItem:after {
    content: "";
    display: block;
    height: 5px;
    background-color: var(--inactive-color);
    position: absolute;
    top: 50%;
    right: 50%;
    width: 100%;
    -webkit-transform: perspective(1px) translateY(-50%);
            transform: perspective(1px) translateY(-50%);
}

.StepCounterItem:after {
    width: 0%;
    z-index: 2;
    left: -50%;
    background-color: var(--active-color);
    -webkit-transition: width var(--transition-track-time) ease-in-out;
    -o-transition: width var(--transition-track-time) ease-in-out;
    transition: width var(--transition-track-time) ease-in-out;
    opacity: 1;
}

.StepCounterItem.is-active:after {
    width: 100%;
    opacity: 1;
    -webkit-transition: width var(--transition-track-time) var(--transition-track-delay) ease-in-out;
    -o-transition: width var(--transition-track-time) var(--transition-track-delay) ease-in-out;
    transition: width var(--transition-track-time) var(--transition-track-delay) ease-in-out
}

.StepCounterItem__label {
    font-weight: 800;
    position: absolute;
    top: -40px;
    left: 50%;
    -webkit-transform: perspective(1px) translateX(-50%);
            transform: perspective(1px) translateX(-50%);
    color: var(--inactive-color);
    -webkit-transition: color 150ms ease-out;
    -o-transition: color 150ms ease-out;
    transition: color 150ms ease-out
}

.StepCounterItem.is-active .StepCounterItem__label {
    color: var(--active-color);
    -webkit-transition: color var(--transition-label-time) var(--transition-label-delay) ease-in;
    -o-transition: color var(--transition-label-time) var(--transition-label-delay) ease-in;
    transition: color var(--transition-label-time) var(--transition-label-delay) ease-in
}

.StepCounterItem__bullet {
    display: block;
    border: 10px solid var(--inactive-color);
    height: 40px;
    width: 40px;
    background-color: white;
    border-radius: 50%;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    position: relative;
    left: 50%;
    -webkit-transform: perspective(1px) translateX(-50%);
            transform: perspective(1px) translateX(-50%);
    z-index: 10;
    -webkit-transition: border-color var(--transition-bullet-time) ease-out;
    -o-transition: border-color var(--transition-bullet-time) ease-out;
    transition: border-color var(--transition-bullet-time) ease-out
}

.StepCounterItem.is-active .StepCounterItem__bullet {
    border-color: var(--active-color);
    -webkit-transition: border-color var(--transition-bullet-time) var(--transition-bullet-delay) ease-in;
    -o-transition: border-color var(--transition-bullet-time) var(--transition-bullet-delay) ease-in;
    transition: border-color var(--transition-bullet-time) var(--transition-bullet-delay) ease-in
}

.StepCounterItem__bullet:hover {
    cursor: pointer;
}


